.ant-table-thead > tr > th {
  border-bottom: 0px;
  font-weight: 600;
  font-size: 11px;
  color: #9B9B9B;
  letter-spacing: 0;
  line-height: 13px;
  position: sticky;
  top: 0px;
  background: white !important;
  z-index: 9;
}
.ant-table-placeholder {
  border-top: 1px solid #F5F6FA;
}
.empty-table {
  flex-direction: column;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 500;
  font-size: 18px;
  color: #0d1b2a;
  letter-spacing: 0;
  text-align: center;
}
.empty-table .svg-responsive {
  max-width: 30%;
  padding-bottom: 30%;
}
.empty-table > * {
  margin-bottom: 30px;
}
.ant-table-thead > tr,
.ant-table-tbody > tr {
  background: white;
  border: 0px;
}
.ant-table-thead > tr.ant-table-row-hover:not(.ant-table-expanded-row) > td,
.ant-table-tbody > tr.ant-table-row-hover:not(.ant-table-expanded-row) > td,
.ant-table-thead > tr:hover:not(.ant-table-expanded-row) > td,
.ant-table-tbody > tr:hover:not(.ant-table-expanded-row) > td {
  background: none;
}
.ant-table-thead > tr > td,
.ant-table-tbody > tr > td {
  border: 0;
  border-bottom: 1px solid rgba(0, 116, 249, 0.15);
  font-weight: 400;
  font-size: 14px;
  color: #0d1b2a;
  letter-spacing: 0;
}
.ant-table-thead > tr > td.table-link,
.ant-table-tbody > tr > td.table-link {
  text-transform: uppercase;
  text-decoration: underline;
  color: #0053FA;
  font-weight: 600;
  font-size: 14px;
  letter-spacing: 0;
}
.ant-table-thead > tr > td.table-link .disabled,
.ant-table-tbody > tr > td.table-link .disabled {
  color: #0d1b2a;
}
.ant-table-thead > tr > td.td-pointer,
.ant-table-tbody > tr > td.td-pointer {
  cursor: pointer;
}
.ant-table-thead .ant-table-row,
.ant-table-tbody .ant-table-row {
  height: 59px;
}
