.table-invoices .ant-table table {
  border-collapse: separate;
  border-spacing: 0px 10px;
}
.table-invoices .ant-table-tbody .ant-tag {
  margin-right: 0;
}
.table-invoices .ant-table-tbody > tr {
  cursor: pointer;
}
.table-invoices .ant-table-tbody > tr.ant-table-row-selected {
  background: #F7FAFE;
  box-shadow: 0 1px 10px 0 rgba(0, 116, 249, 0.1);
  border: 1px solid #DDECFD;
}
.table-invoices .ant-table-tbody > tr.ant-table-row-selected td {
  background: transparent;
  border: 0;
  border-top: 1px solid #DDECFD;
  border-bottom: 1px solid #DDECFD;
}
.table-invoices .ant-table-tbody > tr.ant-table-row-selected td:first-child {
  border-left: 1px solid #DDECFD;
}
.table-invoices .ant-table-tbody > tr.ant-table-row-selected td:last-child {
  border-right: 1px solid #DDECFD;
}
.table-invoices .ant-table-tbody > tr > td {
  border: 0;
  border-bottom: 1px solid rgba(0, 116, 249, 0.15);
}
.table-invoices .ant-table-tbody > tr.tr-invoice-loading {
  background: repeating-linear-gradient(45deg, #ECECEC, #ECECEC 2px, #ffffff 2px, #ffffff 7px);
}
.table-invoices .ant-table-tbody > tr.tr-invoice-loading > td {
  background: none;
}
.table-invoices .ant-table-tbody > tr.tr-invoice-loading:not(.ant-table-row-selected) > td {
  border: 0;
  border-top: 1px solid #DFDFDF;
  border-bottom: 1px solid #DFDFDF;
}
.table-invoices .ant-table-tbody > tr.tr-invoice-loading:not(.ant-table-row-selected) > td:first-child {
  border-left: 1px solid #DFDFDF;
}
.table-invoices .ant-table-tbody > tr.tr-invoice-loading:not(.ant-table-row-selected) > td:last-child {
  border-right: 1px solid #DFDFDF;
}
.table-invoices .ant-checkbox-inner {
  border: 1px solid #77B7FF;
  border-radius: 1px;
}
.table-invoices .ant-checkbox-checked .ant-checkbox-inner:after,
.table-invoices .ant-checkbox-checked:after,
.table-invoices .ant-checkbox-inner:after {
  display: block;
}
.table-invoices th.td-cta,
.table-invoices th.td-total,
.table-invoices th.td-error,
.table-invoices th.td-importAt,
.table-invoices th.td-status,
.table-invoices th.td-dueDate {
  text-align: center;
}
.table-invoices td .ant-tag {
  margin: 0 !important;
}
.table-invoices td.td-actions {
  padding: 0 !important;
  text-align: center;
}
.table-invoices td.td-number {
  font-weight: 600;
  font-size: 12px;
  color: #989CA8;
  letter-spacing: 0;
}
.table-invoices td.td-receiverTitle {
  font-weight: 500;
  font-size: 14px;
  color: #0d1b2a;
  letter-spacing: 0;
}
.table-invoices td.td-companyEmitter,
.table-invoices td.td-companyReceiver {
  font-weight: 400;
  font-size: 12px;
  color: #0d1b2a;
  letter-spacing: 0;
}
.table-invoices td.td-createdAt,
.table-invoices td.td-dueDate,
.table-invoices td.td-importAt {
  font-weight: 600;
  font-size: 12px;
  color: #989CA8;
  letter-spacing: 0;
  text-align: center !important;
}
.table-invoices td.td-totalWoT,
.table-invoices td.td-total {
  font-weight: 700;
  font-size: 14px;
  color: #0d1b2a;
  letter-spacing: 0;
  text-align: center !important;
}
.table-invoices td .table-color {
  color: #0053FA;
}
.table-invoices td .table-color-payin {
  color: #5DBE42;
}
.table-invoices td .table-color-payout {
  color: #FF5F5F;
}
.table-invoices td.td-status {
  text-align: center !important;
}
.table-invoices td.td-error {
  text-align: center !important;
}
.table-invoices td.td-cta {
  text-align: center !important;
}
.table-invoices td.td-cta .ant-btn {
  display: inline-block !important;
}
.table-invoices td.td-concatened {
  font-weight: 600;
  font-size: 10px;
  color: #989CA8;
  letter-spacing: 0;
}
.table-invoices td.td-concatened .title {
  font-weight: 500;
  font-size: 14px;
  color: #0d1b2a;
  letter-spacing: 0;
}
.table-invoices td.td-concatened .subtitle {
  font-weight: 400;
  font-size: 10px;
  color: #0d1b2a;
  letter-spacing: 0;
}
.table-invoices td.td-chevron {
  padding: 0 !important;
}
.table-invoices td.td-chevron svg {
  color: #0d1b2a;
}
.table-invoices td.td-createdDate,
.table-invoices td.td-description {
  font-weight: 400;
  font-size: 14px;
  color: #0d1b2a;
  letter-spacing: 0;
}
.table-invoices td.td-amount {
  font-weight: 700;
  font-size: 14px;
  color: #B5B9C5;
  letter-spacing: 0;
}
